<template>
	<div class="auth_page">
		<b-container>
			<b-navbar class="app-navbar p-0">
				<b-navbar-brand to="/">
					<Logo />
				</b-navbar-brand>
			</b-navbar>
			<b-card no-body class="auth_card border-0 mx-auto">
				<h4 class="mb-5 text-center">Login</h4>
				<b-form @submit.prevent="submit">
					<b-form-group label="Email" label-for="email">
						<b-form-input id="email" type="email" v-model="login.email" />
					</b-form-group>
					<b-form-group label="Password" label-for="password">
						<div class="password_wrapper">
							<b-form-input ref="password" id="password" :type="passwordType" v-model="login.password" :class="{ is_invalid: validateState('password') }" />
							<span class="icon_show_password" :class="{ show: isPasswordShown }" @click.prevent="showPassword">
								<b-icon icon="eye" scale="1.2" />
							</span>
						</div>
					</b-form-group>
					<router-link to="/forget" class="text-decoration-none mb-3 d-inline-block fs-sm mt-3">Forget password?</router-link>
					<b-btn type="submit" variant="primary" block>Login</b-btn>
				</b-form>
				<div class="auth_card_footer text-decoration-none fs-sm text-center">
					<div class="mb-4 mt-5">
						<router-link to="/login" class="text-decoration-none">or login with your calender provider</router-link>
					</div>
					<div class="my-4">
						<span class="text-muted">Don't have an account?</span>&nbsp;
						<router-link to="/signup" class="text-decoration-none">Sign up</router-link>
					</div>
				</div>
			</b-card>
		</b-container>
	</div>
</template>

<script>
	import Logo from "@/components/layouts/Logo.vue";
	import { validationMixin } from "vuelidate";
	import { required, email, minLength, maxLength } from "vuelidate/lib/validators";
	export default {
		components: { Logo },

		mixins: [validationMixin],

		data() {
			return {
				login: { email: "", password: "" },

				passwordType: "password",

				errors: {},
			};
		},

		validations: {
			login: {
				email: { required, email },
				password: { required, minLength: minLength(8), maxLength: maxLength(20) },
			},
		},

		computed: {
			isPasswordShown() {
				return this.passwordType === "text";
			},
		},

		methods: {
			showPassword() {
				this.passwordType = this.passwordType === "password" ? "text" : "password";
			},

			submit() {
				this.$v.$touch();

				if (this.$v.$invalid) return;

				console.log(this.login);
			},

			validateState(field) {
				return this.$v.login[field].$dirty && this.$v.login[field].$invalid;
			},
		},
	};
</script>
